import {Component, OnInit, Input} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import {UniversityService} from '../university.service';
import {Teacher, Department, Degree, Title, College} from '../entity';
@Component({
  selector: 'app-update-teacher',
  templateUrl: './update-teacher.component.html',
})
export class UpdateTeacherComponent implements OnInit {
  @Input() departments: Department[];
  teacher: Teacher;
  @Input() degrees: Degree[];
  @Input() titles: Title[];
  @Input() schools: College[];
  school: College;

  constructor(
    private route: ActivatedRoute,
    private teacherService: UniversityService,
    private location: Location
  ) {
  }

  ngOnInit() {
    this.getDepartments(); // 下拉菜单赋值
    this.getDegrees();
    this.getTitles();
    this.getSchools();
    const id = this.route.snapshot.params.id; // 要修改专业的id
    this.teacherService.getTeacher(id)
      .subscribe(res => {
        this.teacher = res;
      });
  }

  getDepSch(id: number): void {
    this.teacherService.getDepSch(id).subscribe(res => this.departments = res);
  }

  // @ts-ignore
  compareFn(o1: College, o2: College): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }
  getSchools(): void {
    this.teacherService.getColleges()
      .subscribe(res => this.schools = res);
  }

  // @ts-ignore
  // tslint:disable-next-line:adjacent-overload-signatures
  compareFn(o1: Title, o2: Title): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }

  getTitles(): void {
    this.teacherService.getTitles()
      .subscribe(res => this.titles = res);
  }

  // tslint:disable-next-line:adjacent-overload-signatures
  // @ts-ignore
  // tslint:disable-next-line:adjacent-overload-signatures
  compareFn(o1: Degree, o2: Degree): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }

  getDegrees(): void {
    this.teacherService.getDegrees()
      .subscribe(res => this.degrees = res);
  }

  // tslint:disable-next-line:adjacent-overload-signatures
  // @ts-ignore
  // tslint:disable-next-line:adjacent-overload-signatures
  compareFn(o1: Department, o2: Department): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }

  getDepartments(): void {
    this.teacherService.getDepartments()
      .subscribe(res => this.departments = res);
  }

  save(): void { // 多个文本框数据
    console.log(this.teacher);
    this.teacherService.updateTeacher(this.teacher)
      .subscribe((res) => {
        alert(res.message);
        this.goBack();
      });
  }

  goBack(): void {
    this.location.back();
  }
}
