import { Component, OnInit, Input} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import {UniversityService} from '../university.service';
import {College, Department, Title, Degree, Teacher1} from '../entity';
@Component({
  selector: 'app-add-teacher',
  templateUrl: './add-teacher.component.html'
})
export class AddTeacherComponent implements OnInit {
  @Input() departments: Department[];
  @Input() titles: Title[];
  @Input() degrees: Degree[];
  @Input() teacher: Teacher1;
  @Input() schools: College[];
  school: College;
  title: Title;
  degree: Degree;
  department: Department;
  constructor(
    private route: ActivatedRoute,
    private teacherService: UniversityService,
    private location: Location
  ) { }

  ngOnInit() {
    this.getDepartments();
    this.getDegrees();
    this.getTitles();
    this.getSchools();
  }
  getDepSch(id: number): void {
    this.teacherService.getDepSch(id).subscribe(res => this.departments = res);
  }

  getSchools(): void {
    this.teacherService.getColleges()
      .subscribe(res => this.schools = res);
  }
  getDepartments(): void {
    this.teacherService.getDepartments()
      .subscribe(res => this.departments = res);
  }
  getDegrees(): void {
    this.teacherService.getDegrees()
      .subscribe(res => this.degrees = res);
  }
  getTitles(): void {
    this.teacherService.getTitles()
      .subscribe(res => this.titles = res);
  }
  save(name: string, department: Department, title: Title, degree: Degree): void {
    this.department = department;
    this.title = title;
    this.degree = degree; // 多个文本框数据
    // @ts-ignore
    // tslint:disable-next-line:max-line-length
    this.teacher = {name: name.trim(), department: this.department, title: this.title, degree: this.degree};
    if (!name) { return; }
    this.teacherService.addTeacher(this.teacher)
      .subscribe(() => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }
}
