import { Component, OnInit } from '@angular/core';
import { College } from '../entity';
import {UniversityService} from '../university.service';

@Component({
  selector: 'app-college',
  templateUrl: './college.component.html',
  styleUrls: ['./college.component.css']
})
export class CollegeComponent implements OnInit {
  colleges: College[];
  constructor(private schoolService: UniversityService) { }

  ngOnInit() {
    this.getColleges();
  }
  // 调用service中的获取数据方法，如果获取数据成功，把数据res指向的赋给this.colleges（this.colleges前台可以使用）
  getColleges(): void {
    this.schoolService.getColleges ()
      .subscribe(res => this.colleges = res);
  }
  delete(college: College | number): void {
    this.colleges = this.colleges.filter(h => h !== college);
    this.schoolService.deleteCollege(college).subscribe(res => alert(res.message));
  }
}
