import { Component, OnInit, Input } from '@angular/core';
// 路由
import { ActivatedRoute } from '@angular/router';
// 返回
import { Location } from '@angular/common';
// service
import { UniversityService } from '../university.service';

@Component({
  selector: 'app-add',
  templateUrl: './add-degree.component.html',
  styleUrls: ['./add-degree.component.css']
})
// 处理保存和返回的事件
export class AddDegreeComponent implements OnInit {
  // @装饰器需要导入 college要和文本框联系
  @Input() degree: { no: string; description: string; remarks: string };
  // 导入后 需要声明
  constructor(
    private route: ActivatedRoute,
    private degreeService: UniversityService,
    private location: Location
  ) {}

  ngOnInit(): void {}
  // college1（无id)重新在本类中定义，不同于实体类中的college
  save(description: string, no: string, remarks: string): void { // 多个文本框数据
    this.degree = {no: no.trim(), description: description.trim(), remarks: remarks.trim()};
    if (!description) { return; }
    // 通过与servlet接口将带数据的对象传至后台
    this.degreeService.addDegree(this.degree)
      .subscribe(() => this.goBack());
  }

  goBack(): void {
    this.location.back();
  }
}
